.buttons {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 2px;
    background-color: white;
    display: flex;
    justify-content: space-around;
    column-gap: 10px;
  }
  
  .btn {
    background-color: #00748f;
    color: white;
    padding: 12px;
    border-color: #dedede #bababa #aaa #bababa;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    cursor: pointer;
    width: 80px;
  }